import {post, get} from ".";

// const httpProxyApi = '/manage/http_proxy/http_proxy';
const loginApi = '/user/login';
const check_tokenApi = '/auth/oauth/check_token';
const findUserApi = '/user/findUser';
const findUserInfoApi = '/user/findUserInfo';
const accessControlApi = '/user/accessControl';
const addRecordApi = '/user/addRecord';
const updateRemarkApi = '/user/updateRemark';
const findlogmanagementApi = '/user/findlogmanagement';
const findDepartApi = '/department/findDepart';
const editusersApi = '/user/editusers';


// export const fetchHttpProxyApi = ((url, method, data) => (get(httpProxyApi, {url, method, data})));

export const fetchLogin = (username, password, grant_type = 'password', flag = 'admin') => (
  post(loginApi, {username, password, grant_type, flag}));

export const fetchCheck_token = (token) => (post(check_tokenApi, {token}));

export const fetchFindUser = (pageStart, pageSize, express, status, authState, deptIds) => (
  post(findUserApi, {pageStart, pageSize, express, status, authState, deptIds}));

export const fetchFindUserInfo = (userid) => (post(findUserInfoApi, {userid}));

export const fetchAccessControl = (userid, on_off) => (post(accessControlApi, {userid, on_off}));

export const fetchAddRecord = (userid, remark, beginAuthTime, endAuthTime) => (post(addRecordApi, {userid, remark, beginAuthTime, endAuthTime}));

export const fetchUpdateRemark = (id, remark) => (post(updateRemarkApi, {id, remark}));

export const fetchFindlogmanagement = (pageStart, pageSize, flag, loginTime, logoutTime, express, userid) => (post(findlogmanagementApi, {pageStart, pageSize, flag, loginTime, logoutTime, express, userid}))

export const fetchfindDepart = (() => (get(findDepartApi, {})));

export const fetchEditusers = (uids) => (post(editusersApi, {uids}));


